        >
      </el-col>
    </el-aside>
    <el-container>
      <el-header>
        <el-col class="optionModule" :span="2" :xs="3">
          <el-popover
            ref="popover"
            placement="right"
            width="200"
            trigger="click"
          >
            <div>
              <div id="topTags" class="hotTag">
                <el-tag
                  class="movieTag"
                  v-for="tag in hotTags"
                  :key="tag"
                  @click="addTag(tag)"
                  >{{ tag }}
                </el-tag>
              </div>
              <el-tag
                class="filterTag inlineTag"
                :key="tag"
                v-for="tag in filterTags"
                closable
                :disable-transitions="false"
                @close="closeTag(tag)"
              >
                {{ tag }}
              </el-tag>
              <el-input
                class="inputNewTag"
                v-if="tagInputVisible"
                v-model="tagInputValue"
                ref="saveTagInput"
                size="small"
                @keyup.enter.native="handleTagInputConfirm"
                @blur="handleTagInputConfirm"
              >
              </el-input>
              <el-button
                v-else
