        "
      />
      <span style="margin-left: 10px; display: inline-block; width: 250px"
        >将此web app安装到本机，点击
        <img
          width="18"
          height="18"
          style="display: inline-block"
          src="data:image/png;base64,"
        />
        添加到主屏幕</span
      >
    </div>
  </el-container>
