<script>
// import func from "../../vue-temp/vue-editor-bridge";
const axios = require("axios");
export default {
  name: "home",
  data() {
    return {
      api: process.env.VUE_APP_API,
      token: "",
      loading: false,
      movies: [],
      type: "",
      page: 1,
      limit: 40,
      q: "",
      order_by: "year",
      movieShow: false,
      noMore: false,
      filterTags: [],
      tagInputVisible: false,
      tagInputValue: "",
      hotTags: [
        "美国",
        "中国大陆",
        "香港",
        "日本",
        "韩国",
        "剧情",
        "经典",
        "人性",
        "犯罪",
        "悬疑",
        "科幻",
        "喜剧",
        "2022",
        "2021",
        "2000",
        "1995",
        "贾樟柯",
        "杨德昌",
        "克里斯托弗·诺兰",
        "爱德华·诺顿"
      ],
    };
  },
  computed: {
    //显示PWA提示
    webAppTip: function () {
      if (this.isIos() && !this.isInStandaloneMode()) {
        return true;
      }
      return false;
    },

    optionButtonStyle: function () {
      if (this.filterTags.length > 0) {
        return {
          color: "tomato",
        };
      } else {
        return {
          color: "#606266",
        };
      }
    },
  },
  methods: {
    // 初始化页面
    init: async function () {
      this.getMovies();
    },
    // 监听滚动条
    listenScoller: function () {
      let vm = this;
      window.addEventListener("scroll", function () {
        if (vm.$route.path != "/home") {
          return;
        }
        let scrollTop =
          document.documentElement.scrollTop || document.body.scrollTop;
        let windowHeight =
          document.documentElement.clientHeight || document.body.clientHeight;
        let scrollHeight =
          document.documentElement.scrollHeight || document.body.scrollHeight;
        if (scrollTop + windowHeight + 100 >= scrollHeight) {
          vm.infLoad();
        }
      });
    },
    // 重置请求参数
    reset: function () {
      this.movieShow = false;
      this.movies = [];
      this.type = "";
      this.page = 1;
      this.limit = 40;
      this.q = "";
      this.order_by = "year";
    },
    // 标签变化刷新页面
    refreshByTag: function () {
      this.noMore = false;
      this.page = 1;
      this.movies = [];
      this.getMovies();
    },
    // 获取电影数据
    getMovies: function () {
      let vm = this;
      vm.loading = true;
      let request_body = {
        collection: "movie",
        database: "Douban",
        dataSource: "Cluster0",
        filter: {},
        sort: {},
        skip: (vm.page - 1) * vm.limit,
        limit: vm.limit,
      };
      if (vm.type === "电影") {
        request_body["filter"]["is_tv"] = false;
      }
      if (vm.type === "剧集") {
        request_body["filter"]["is_tv"] = true;
      }
      if (vm.q !== "") {
        request_body["filter"]["title"] = {};
        request_body["filter"]["title"]["$regex"] = vm.q;
        request_body["filter"]["title"]["$options"] = "i";
      }
      request_body["sort"][vm.order_by] = -1;
      request_body["sort"]["_id"] = -1;
      if (vm.filterTags.length > 0) {
        request_body["filter"]["tags"] = {};
        request_body["filter"]["tags"]["$all"] = vm.filterTags;
      }

      const headers = {
        "Content-Type": "application/json",
        "Access-Control-Request-Headers": "*",
        "api-key":
          "LLppSL7L7bjMm7uHavkXOICu9iymDvwn51rADdUM7hXDjEhxVGZ8zPRqnKOdnLu8",
      };

      setTimeout(function () {
        axios
          .post("https://dataapi.8610000.xyz", JSON.stringify(request_body), {
            headers: headers,
          })
          .then(function (response) {
            if (response.data.documents.length == 0) {
              vm.noMore = true;
            }
            response.data.documents.forEach((movie) => {
              vm.movies.push(movie);
              vm.movieShow = true;
            });
            setTimeout(function () {
              vm.loading = false;
            }, 400);
          })
          .catch(function (error) {
            console.log(error);
            vm.loading = false;
            vm.init(true);
          });
      }, 1000);
    },
    // 菜单选择重新获取数据
    menuSelect: function (index) {
      this.reset();
      if (index === "3") {
        this.type = "剧集";
      } else if (index === "2") {
        this.type = "电影";
      } else {
        this.type = "";
      }
      this.getMovies();
    },
    // 滚动条变化加载新数据
    infLoad: function () {
      if (this.noMore || this.loading) {
        return;
      }
      this.page++;
      this.getMovies();
    },
    // 电影排序
    sort: function (key) {
      this.reset();
      this.order_by = key;
      this.getMovies();
    },
    //电影搜索
    search: function () {
      this.page = 1;
      this.movies = [];
      this.getMovies();
    },
    // 跳转电影对应页面
    goToMovie: function (id) {
      this.$router.push({ path: `/movie/${id}` });
    },
    //新增筛选标签
    addTag: function (tag) {
      if (this.filterTags.indexOf(tag) === -1) {
        this.filterTags.push(tag);
        this.refreshByTag();
      }
    },
    // 去除筛选标签
    closeTag: function (tag) {
      let index = this.filterTags.indexOf(tag);
      this.filterTags.splice(index, 1);
      this.refreshByTag();
    },
    // 显示标签输入框
    showTagInput: function () {
      this.tagInputVisible = true;
      this.$nextTick((_) => {
        console.log(_);
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    // 输入标签确认
    handleTagInputConfirm: function () {
      let inputValue = this.tagInputValue;
      if (inputValue !== "" && this.filterTags.indexOf(inputValue) === -1) {
        this.filterTags.push(inputValue);
        this.refreshByTag();
      }
      this.tagInputVisible = false;
      this.tagInputValue = "";
    },
    // UA检测
    isIos: function () {
      let userAgent = window.navigator.userAgent.toLowerCase();
      return /iphone|ipad|ipod/.test(userAgent);
    },
    // Standalone检测
    isInStandaloneMode: function () {
      return "standalone" in window.navigator && window.navigator.standalone;
    },
  },
  mounted() {
    this.init();
    this.listenScoller();
  },
  watch: {
    //监听路由
    $route() {
      if (
        this.$route.path === "/home" &&
        localStorage.newTag &&
        localStorage.newTag != ""
      ) {
        //新标签筛选
        if (this.filterTags.indexOf(localStorage.newTag) === -1) {
          this.filterTags.push(localStorage.newTag);
          this.refreshByTag();
          localStorage.newTag = "";
        }
      }
    },
    filterTags: function () {
      this.noMore = false;
    },
    q: function () {
      this.noMore = false;
    },
  },
};
</script>

